.body{
flex:0.8;
background: linear-gradient(rgb(58, 56, 70),rgba(0,0,0,.9));
color:white;
height: 100vh;
padding: 30px;
overflow-y: overlay;
}
.body::-webkit-scrollbar{
  display: none;
}
.body_info{
  display: flex;
align-items: flex-end;
padding: 10px;

}
.body_infotext{
  flex:1;
}
.body_infotext >h2{
font-size: 40px;
margin-bottom: 10px;
}
.body_infotext >p{
font-size:14px; 
}
.body_info >img{
height:170px ;
margin: 0 20px;
box-shadow: 0 4px 60px rgba(0,0,0,.5);
}
.body_icon{
  display: flex;
  align-items: center;
}
.body_icon> .MuiSvgIcon-root{
margin-right:20px;
}
.body_shuffle{
  font-size: 60px !important;
  margin-left: 50px;
  margin-top:20px;
  margin-bottom: 20px;
}
.body_shuffle:hover{
  transition: 100ms transform ease-in;
  transform: scale(1.08);
}
.body_song{
  margin: 20px -30px;
}